import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import advertising from '@/components/advertising'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/advertising',
      name: 'advertising',
      component: advertising
    }
  ]
})



