<template>
  <div>
    <div>
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <div>
            <iframe width="100%" height="300" frameborder="0" scrolling="auto"
                    :src="newurl"></iframe>
            <!--http://lmsjy.qq.com/uwMROfz2r5zAoaQXGdGnC2dfiMVaprY8QrbK21CByPIM2CdQ/a0031fror7v.mp4?vkey=531B8D482144F8526F3761D2A31027CA6CC097D061045329DE96F8673097E77FFA1BFE128D04AE72A3928DFA2345308E9100132D49A67A2197F4161F7EFB5FA6E9CC5601F2E23FD9DDE5B62854312F83490CE88035A95F9DA6993A48E4E03338B904F90A416470C35CC2C9753DD96068-->
          </div>
        </el-col>
      </el-row>
    </div>

    <div style="margin-top: 10px;">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <div>
            <el-input v-model="input" placeholder="请输入视频链接"></el-input>
          </div>
        </el-col>
      </el-row>
    </div>

    <div style="margin: 10px 0 0 0;">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <div>
            <el-button type="primary" style="width: 100%;" @click="onClick">确定</el-button>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import $ from 'jquery'
  export default {
    data() {  //组件的数据区
      return {
        input: `https://v.qq.com/x/cover/xpu952oo5tr9yzl/h00317igalv.html?ptag=qqbrowser`,
        url: `http://lmsjy.qq.com/uwMROfz2r5zAoaQXGdGnC2dfiMVaprY8QrbK21CByPIM2CdQ/`,
        newurl: ``
      }
    },
    methods: {  //方法区
      onClick() {
        let _this = this;
        for (let i of this.input) {
          console.log(i);
          if (i === '?') {
            let str = this.input.substring(this.input.length - 31);
            let newstr = str.substring(0, 11);

            $.ajax({
              url: 'http://vv.video.qq.com/getkey?format=2&otype=json&vt=150&vid='+newstr+'&ran=0\%2E9477521511726081\\&charge=0&filename=x0164ytbgov.mp4&platform=11',
              dataType:"jsonp",
              success:function (res) {
                _this.newurl = _this.url + res.filename + `?vkey=` + res.key;
                let con = res.key;
                console.log('内容',_this.newurl)
              },
            });
            /*axios.post(``).then(res => {
              console.log('返回', res.data.filename);
              console.log('返回', res.data.key);
              this.newurl = this.url + res.data.filename + `?vkey=` + res.data.key
            }).catch(error=>{
              console.log('报错',error)
            })*/
          }
        }
      }
    },
    components: {  //子组件进行标签映射

    },
    mounted() { //页面初始化完成

    }
  }
</script>

<style>

</style>



